import { GetHostedPath } from "../lib/FrontEndUtils";
import { PostType } from "../types/post";

export interface PopularStoriesProps {
    stories: Array<PostType>
}

const divStyle1 = {
    backgroundImage: "url(./assets/img/demo/1.jpg)",
    height: '150px',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat'
}

export const PopularStories = (props: PopularStoriesProps): JSX.Element => {
    return <>
    <ol className="list-featured">
        {props.stories.map((story: PostType) => {
            return <>
            <li>
				<span>
				<h6 className="font-weight-bold">
				<a href={`${GetHostedPath()}/blog/${story.slug}`} className="text-dark">{story.title}</a>
				</h6>
				<p className="text-muted">
					{story.author}
				</p>
				</span>
				</li>
            </>
        })}
    </ol>
    </>
}