import { format, parseISO } from 'date-fns';
import { GetStaticProps } from 'next';
import Link from 'next/link';
import React from 'react';
import { AllStories } from '../components/AllStories';
import { FeaturedStory } from '../components/FeaturedStory';
import { HighlightedStories } from '../components/HighlightedStories';
import Layout from '../components/Layout';
import { PopularStories } from '../components/PopularStories';
import { getAllPosts } from '../lib/api';
import { GetHostedPath } from '../lib/FrontEndUtils';
import { PostType } from '../types/post';
import { GetPostConfig } from '../utils/mdxUtils';

type IndexProps = {
  posts: any;
};


export const Index = ({ posts }: IndexProps): JSX.Element => {
  // console.trace(posts);
  return (
    <>

      <Layout>
        {/* {posts['featured'] && <FeaturedStory
          slug={posts['featured'].slug}
          title={posts['featured'].title}
          tags={posts['featured'].tags}
          description={posts['featured'].description}
        />} */}

        {posts['featured'] && <FeaturedStory {...posts['featured']}

        />}

        {posts['highlight'] &&
          <HighlightedStories main={[]}

            secondary={[]} />

        }
        <div className="container">
          <div className="row justify-content-between">
            <div className="col-md-8">
              <h5 className="font-weight-bold spanborder"><span>All Stories</span></h5>
              {posts['all'] &&
                <AllStories stories={posts['all']} />
              }
            </div>
            {posts['popular'] && posts['popular'].length > 0 && <div className="col-md-4 pl-4">
              <h5 className="font-weight-bold spanborder"><span>Popular</span></h5>
              <PopularStories stories={posts['popular']} />
            </div>}
          </div>
        </div>



        {false && posts['all'].map((post) => (
          <article key={post.slug} className="mt-12">
            <p className="mb-1 text-sm text-gray-500 dark:text-gray-400">
              {/* {format(parseISO(post.date), 'MMMM dd, yyyy')} */}
            </p>
            <h1 className="mb-2 text-xl">
              <Link as={`${GetHostedPath()}/blog/${post.slug}`} href={`${GetHostedPath()}/blog/[slug]`}>
                {post.title}
              </Link>
            </h1>
            <p className="mb-3">{post.description}</p>
            <p className="mb-3 text-xs">{post.tags ? post.tags.split(",").map((tag: string) => { return <span key={tag} className='tag'><a href={`/tag/${tag.trim()}`}>{`#${tag.trim()}`}</a></span> }) : ""}</p>
            <p>
              <Link as={`${GetHostedPath()}/blog/${post.slug}`} href={`${GetHostedPath()}/blog/[slug]`}>
                Read More
              </Link>
            </p>
          </article>
        ))}
      </Layout>
    </>
  );
};

export const getStaticProps: GetStaticProps = async () => {
  const allPosts = getAllPosts(['title', 'description', 'author', 'date', 'image', 'slug']) || [];
  console.trace(allPosts);
  const configJson = GetPostConfig();
  const featured = allPosts.filter((post) => {
    return post.slug === configJson['featured'];
  });
  const posts = {};
  if (featured.length > 0) {
    posts['featured'] = featured[0];
  }

  const popular = allPosts.filter((post) => {
    return configJson['popular'].includes(post.slug);
  });
  if (popular.length > 0) {
    posts['popular'] = popular;
  }
  posts['all'] = allPosts;
  console.trace(posts);
  return {
    props: { posts },
  };
};

export default Index;
