import { GetHostedPath } from "../lib/FrontEndUtils";
import { PostType } from "../types/post";

export interface AllStoriesProps {
    stories: Array<PostType>
}

const divStyle1 = {
    backgroundImage: "url(./assets/img/demo/1.jpg)",
    height: '150px',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat'
}

export const AllStories = (props: AllStoriesProps): JSX.Element => {
    return <>
        {props.stories.map((story: PostType) => {
            return <>
                <div className="mb-3 d-flex justify-content-between">
                    <div className="pr-3">
                        <h2 className="mb-1 h4 font-weight-bold">
                            <a className="text-dark" href={`${GetHostedPath()}/blog/${story.slug}`}>{story.title}</a>
                        </h2>
                        <p>
                            {story.description ? story.description : ""}
                        </p>
                        <div className="card-text text-muted small">
                            {"Ajay"}
                        </div>
                        {story.date && <small className="text-muted">{story.date}</small>}
                    </div>
                    {story.image && <img height="120" src={`./assets/img/${story.image}`} />}
                </div>

            </>
        })}
    </>
}